/*Experiment*/
:root {
  --Theme: #c6c6c6;
  --Theme-scroller: #b4d2cd;
  --theme-secondary: #3a3f44;
}

.nav {
  width: 100%;
  height: fit-content;
  z-index: 100;

  display: flex;
}

.body {
  background-color: var(--Theme);
  display: flex;
}
